html, body, #root {
  width:100%;
  height:100%;
  margin: 0;
  padding: 0;
  position: relative;
}


#header {
  position: relative;
  z-index: 1;
  background-color: white;
}

#bottom {
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;;
}

#map {
  z-index: 0;
  position: fixed;
  top:0;
  width: 100%;
  height: 100%;
  background-color: green;
}

